<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2022-3-8 08:53:38
 * @Description: 运维监控大屏-地图组件（世界地图）
 * @Description：copy from @\pages\screenData\customerMonitoringScreen\components\MapComponentBmap\index.vue
-->
<template>
  <div class="map-component">
    <div class="map-container" ref="myEChart"></div>
    <i
      @click="backHandler"
      title="恢复地图原始状态"
      class="map-component-reset el-icon-refresh-left"
      v-show="backBtnShow"
    ></i>
    <el-card class="map-component-card box-card">
      <el-row class="title">机组租赁状态：</el-row>
      <el-row>
        <el-col v-for="(item, index) in legend.rentState" :key="index" :span="8">
          <svg-icon :icon-class="item.icon"></svg-icon>{{ item.name }}
        </el-col>
      </el-row>
      <!-- 2022-3-4 16:58:04 Demon 以饼图颜色标注即可 -->
      <!-- <el-row class="title">机组运行状态：</el-row> -->
      <!-- <el-row>
        <el-col
          v-for="(item, index) in legend.operatingState"
          :key="index"
          class="container"
          :span="8"
        >
          <div :style="{ backgroundColor: item.color }" class="color-box"></div>
          {{ item.name }}
        </el-col>
      </el-row> -->
    </el-card>
  </div>
</template>
<script>
// 百度地图开发 sdk key
import loadBMap from '@/utils/loadBMap.js'
import _svgPath from './svgPath'

export default {
  data() {
    return {
      // 图例的 svg path
      svgPath: _svgPath,
      backBtnShow: true,
      zoom: 5,
      center: [103.114129, 36.750339],
      myEChart: null,
      bmap: null,
      BMap: null,
      // 地图图例
      legend: {
        rentState: [
          {
            icon: 'mso-waitingForRent',
            name: '待租赁'
          },
          {
            icon: 'mso-renting',
            name: '租赁中'
          },
          {
            icon: 'mso-repair',
            name: '维修'
          },
          {
            icon: 'mso-construction',
            name: '施工'
          },
          {
            icon: 'mso-maintenance',
            name: '检修'
          }
        ],
        operatingState: [
          {
            name: '离线',
            color: '#808080'
          },
          {
            name: '停机',
            color: '#f6f6f6'
          },
          {
            name: '风险',
            color: '#8ec9f4'
          },
          {
            name: '故障',
            color: '#fd2e2d'
          },
          {
            name: '运行',
            color: '#20be62'
          }
        ]
      }
    }
  },
  inject: ['colorOperation'],
  created() {
    // const colorOperation = this.colorOperation
    // this.legend.operatingState.map((item, index) => {
    //   item.color = colorOperation[index]
    //   return item
    // })
  },
  mounted() {
    // this.renderMap();
  },
  props: {
    // 地图数据
    mapdata: {
      type: Object,
      default: (_) => {}
    },
    documentWidth: {
      type: Number
    }
  },
  watch: {
    mapdata(newVal, oldVal) {
      this.renderMap()
    },
    documentWidth(n, o) {
      this.renderMap()
    }
  },
  methods: {
    backHandler() {
      // 恢复地图初始状态
      // this.bmap.centerAndZoom(new this.BMap.Point(this.center[0], this.center[1]), 5)
      this.bmap.reset()
      // this.backBtnShow = false
    },
    renderMap() {
      const mapdata = this.mapdata
      loadBMap('o7gALXwtch6SNk9jjH4DmFffcFt1PhFL')
        .then((e) => {
          this.BMap = e
          // 获取用户计算机所在地址
          let position = [103.98703545360101, 30.627640494477983]
          if (!!navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
              (res) => {
                const coords = !!res.coords && res.coords
                if (coords) {
                  position = [coords.longitude, coords.latitude]
                }
              },
              (err) => {
                console.error(err)
              }
            )
            const point = new e.Point(position[0], position[1])
            const geocoder = new e.Geocoder()
            geocoder.getLocation(point, (res) => {
              const addressComponent = res.addressComponents
              const city = addressComponent.city
              document.querySelector('.city-span').innerHTML = city
            })
          }
          // 百度地图API功能
          this.myEChart ||
            (this.myEChart = this.$echarts.init(this.$refs.myEChart, null, {
              renderer: 'svg'
            }))
          let data = []
          let convertData = function (data) {
            // let res = [];
            // for (let i = 0; i < data.length; i++) {
            //   let geoCoord = geoCoordMap[data[i].name];
            //   if (geoCoord) {
            //     res.push({
            //       name: data[i].name,
            //       value: geoCoord.concat(data[i].value),
            //     });
            //   }
            // }
            //
            // return res;
            const geoCoordMap = mapdata || []
            const resData = []
            for (const key in geoCoordMap) {
              resData.push({
                name: key,
                value: geoCoordMap[key]
              })
            }
            return resData
          }
          const svgPath = _svgPath
          // 地图图例
          const legend = this.legend
          // FIXME 地图缩放中心向左偏移了（页面缩放引起，暂无解决方案）
          let option = {
            backgroundColor: 'transparent',
            tooltip: {
              trigger: 'item'
            },
            bmap: {
              center: this.center,
              zoom: this.zoom,
              roam: true,
              // 2022-5-16 14:01:26 Demon
              // https://lbsyun.baidu.com/cms/jsapi/reference/jsapi_reference_3_0.html#a0b1
              // \node_modules\echarts\extension\bmap\BMapModel.js
              mapOptions: {
                // maxZoom: 8,
                enableMapClick: false
              },
              mapStyle: {
                styleJson: [
                  {
                    featureType: 'water',
                    elementType: 'all',
                    stylers: {
                      color: '#044161'
                    }
                  },
                  {
                    featureType: 'land',
                    elementType: 'all',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'boundary',
                    elementType: 'geometry',
                    stylers: {
                      color: '#064f85'
                    }
                  },
                  {
                    featureType: 'railway',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'geometry',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#005b96',
                      lightness: 1
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'geometry',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#00508b'
                    }
                  },
                  {
                    featureType: 'poi',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'green',
                    elementType: 'all',
                    stylers: {
                      color: '#056197',
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'subway',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'manmade',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'local',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'boundary',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#029fd4'
                    }
                  },
                  {
                    featureType: 'building',
                    elementType: 'all',
                    stylers: {
                      color: '#1a5787'
                    }
                  },
                  // 字体填充
                  {
                    featureType: 'label',
                    elementType: 'labels.text.fill',
                    stylers: {
                      visibility: 'on',
                      color: '#2dc4bbff',
                      weight: '0'
                    }
                    // stylers: {
                    //   color: "#999999",
                    // },
                  },
                  // 字体描边
                  {
                    featureType: 'label',
                    // elementType: "labels.text.fill",
                    elementType: 'labels.text.stroke',
                    stylers: {
                      visibility: 'on',
                      color: '#ffffff00',
                      weight: '1'
                    }
                    // stylers: {
                    //   color: "#999999",
                    // },
                  },
                  // 不展示城市前的图标
                  // {
                  //   featureType: 'city',
                  //   elementType: 'labels',
                  //   stylers: {
                  //     visibility: 'on'
                  //   }
                  // },
                  // 必须在 label option 之后
                  // 区县
                  {
                    featureType: 'district',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  // 乡镇
                  {
                    featureType: 'town',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  // 村庄
                  {
                    featureType: 'village',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'city',
                    elementType: 'labels.icon',
                    stylers: {
                      visibility: 'off'
                    }
                  }
                ]
              }
            },
            series: [
              // {
              //   name: "pm2.5",
              //   type: "scatter",
              //   coordinateSystem: "bmap",
              //   data: convertData(data),
              //   encode: {
              //     value: 2,
              //   },
              //   symbolSize: function (val) {
              //     return val[2] / 10;
              //   },
              //   label: {
              //     formatter: "{b}",
              //     position: "right",
              //   },
              //   itemStyle: {
              //     color: "#ddb926",
              //   },
              //   emphasis: {
              //     label: {
              //       show: true,
              //     },
              //   },
              // },
              {
                name: '',
                type: 'scatter',
                // type: 'effectScatter',
                coordinateSystem: 'bmap',
                data: convertData(data),
                symbol: (value, params) => {
                  const rentState = value[13]
                  const path = 'path://' + svgPath[legend.rentState[rentState].icon.slice(4)]
                  return path
                },
                symbolSize: 16,
                // 注意：series.tooltip 仅在 tooltip.trigger 为 'item' 时有效。
                tooltip: {
                  position: 'top',
                  show: true,
                  formatter: (params, ticket, cb) => {
                    const item = params.data
                    const value = item.value
                    // 12 运行状态 13 租赁状态
                    return `
                <div style="color:#b4e4ff">
                  <div>${value[7]}</div>
                  <div>${legend.operatingState[value[12]].name}
                  ,${legend.rentState[value[13]].name}</div>
                  <div>经度：${value[0]}</div>
                  <div>纬度：${value[1]}</div>
                  <div>${value[2]}</div>
                  <div>所属项目：${value[5]}</div>
                </div>
                `
                  },
                  backgroundColor: 'rgba(2,20,4,1)'
                },
                encode: {
                  value: 2
                },
                // 高亮显示标签
                emphasis: {
                  label: {
                    show: false
                  }
                },
                showEffectOn: 'emphasis',
                rippleEffect: {
                  brushType: 'stroke'
                },
                label: {
                  formatter: '{b}',
                  position: 'right',
                  show: false
                },
                itemStyle: {
                  // color: 'red'
                  opacity: 1,
                  color: (_) => {
                    let color = `#f6f6f6`
                    try {
                      const operatingState = _.data.value[12]
                      const color2 = legend.operatingState[operatingState].color
                      color = color2
                      // const typeNum = _._data.value[4]
                      // let color2 = this.typeColor[typeNum]
                      // color = color2
                    } catch (error) {
                      console.error(error)
                    }
                    return color
                  }
                  // borderColor: '#444',
                  // borderWidth: 3
                  // shadowBlur: 10,
                  // shadowColor: "#333",
                },
                zlevel: 1
                // silent: true
              }
              // {
              //   type: "custom",
              //   coordinateSystem: "bmap",
              //   renderItem: renderItem,
              //   itemStyle: {
              //     opacity: 0.5,
              //   },
              //   animation: false,
              //   silent: true,
              //   data: [0],
              //   z: -10,
              // },
            ]
          }
          this.myEChart.setOption(option)
          // this.myEChart.on('click', (e) => {
          //   // return false
          //   e.event.stopPropagation()
          // })
          // 渲染结束后更新一次，避免刷新后地图恢复初始状态
          // 更新中心点和缩放比例
          this.myEChart.on('finished', () => {
            // 得到百度地图对象
            const bmap = this.myEChart.getModel().getComponent('bmap').getBMap()
            const zoom = bmap.getZoom()
            const center = bmap.getCenter()
            this.zoom = zoom
            this.center = [center.lng, center.lat]
            this.bmap = bmap

            bmap.disableDoubleClickZoom()

            // bmap.addControl(new BMap.NavigationControl()) // 添加平移缩放控件
            // bmap.addControl(new BMap.ScaleControl()) // 添加比例尺控件
            // bmap.enableContinuousZoom() //启用地图惯性拖拽，默认禁用
            // bmap.addControl(new this.BMap.OverviewMapControl()) //添加缩略地图控件
            // bmap.enableScrollWheelZoom() //启用滚轮放大缩小
            // bmap.addControl(new BMap.MapTypeControl()) //添加地图类型控件
            // bmap.disable3DBuilding()
            // bmap.centerAndZoom(point, 14)
            // bmap.setMapStyle({ style: 'normal' }) //设置模板的初始风格
          })
          // this.myEChart.on('click', (e) => {
          //   if (e.componentSubType === 'effectScatter') {
          //     const data = {
          //       displayName: e.value[3],
          //       status: e.value[4],
          //       mchCode: e.value[5]
          //     }
          //     this.$emit(`cb`, data)
          //   }
          // })
        })
        .catch((err) => {})
    }
  }
}
</script>
<style lang="less" scoped>
.map-component {
  height: 100%;
  width: 100%;
  transform: translateY(4px);
  .map-container {
    height: 100%;
    width: 100%;
    /* transform: scale(1.1); */
  }
  &-reset {
    position: absolute;
    right: 50px;
    bottom: 25px;
    background: transparent;
    border: 0;
    color: white;
    font-size: 1.6rem;
    cursor: pointer;
  }
  &-card {
    border: 1px solid #338eeb90;
    bottom: -3px;
    color: #dedede;
    font-size: 1.1rem;
    border-radius: 6px;
    position: absolute;
    left: -22px;
    background-color: #102447;
    transform: scale(0.75);
    transition: 0.5s;
    &:hover {
      // transform: scale(1);
    }
    /deep/ .el-card__body {
      padding: 5px;
      width: 260px;
    }
    .title {
      font-size: 1.3rem;
    }
    svg {
      font-size: 1.7rem;
      margin: 0 4px 0 0;
    }
    .el-col {
      margin: 5px 0;
    }
    .container {
      display: flex;
      .color-box {
        width: 20px;
        height: 20px;
        border-radius: 3px;
        margin: 0 3px;
      }
    }
  }
}
</style>
<style lang="less">
/* 隐藏文字信息 */
.anchorBL {
  display: none;
}
.ec-extension-bmap > div > div > div > div {
  // box-shadow: inset 0px 0px 20px 20px #00124c8f;
}
</style>



