<template>
  <div class="monitoring-screen-operation-bottom">
    <el-row>
      <el-col :span="7" class="content-item">
        <div>维保提示与报警实时监控</div>
        <div>
          <scroll-table
            :canStopFlag="true"
            :column="tableKeepColumn"
            :tableData="tableKeepData"
            :plan="1"
            :title="'点击跳转到报警历史页面'"
            @diagnoseHandler="diagnoseHandler"
          >
          </scroll-table>
        </div>
        <div class="summary">
          待受理：<span>30</span> 处理中：<span>25</span> 已完成：<span>65</span>
        </div>
      </el-col>
      <el-col :span="10" class="content-item">
        <div>设备与报警实时监控</div>
        <div>
          <scroll-table
            :canStopFlag="true"
            :column="tableKeepColumn"
            :tableData="tableAlertData"
            :plan="1"
            :title="'点击跳转到报警历史页面'"
            @diagnoseHandler="diagnoseHandler"
          >
          </scroll-table>
        </div>
        <div class="summary">
          待受理：<span>30</span> 处理中：<span>25</span> 已完成：<span>65</span>
        </div>
      </el-col>
      <el-col :span="7" class="content-item">
        <div>环境风险提示与报警实时监控</div>
        <div>
          <scroll-table
            :canStopFlag="true"
            :column="tableKeepColumn"
            :tableData="tableWeatherData"
            :plan="1"
            :title="'点击跳转到报警历史页面'"
            @diagnoseHandler="diagnoseHandler"
          >
          </scroll-table>
        </div>
        <div class="summary">
          待受理：<span>30</span> 处理中：<span>25</span> 已完成：<span>65</span>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import _data from '../../../pages/screenData/employeeMonitoringScreen/components/ScreenMainCenter/data'
import _data from '../data'
import ScrollTable from '@/components/ScrollTable'
import { formatTime } from '@/utils/common'

export default {
  name: 'MonitoringScreenOperationBottom',
  components: {
    ScrollTable
  },
  props: {
    allData: {
      type: Object,
      default: (_) => {
        return {}
      }
    }
  },
  inject: ['mes'],
  watch: {
    allData: {
      handler: function (nv, ov) {
        const mes = this.mes
        // 维保提醒
        const data1 = !!nv && nv
        if (data1) {
          const tableKeepData = new Array()
          const arr1 = data1.RemindList
          arr1.forEach((item, index) => {
            const obj = {
              content: index + 1 + '. ' + item.ProjectName,
              id: item.GeneratorSetId,
              mchModel: item.GensetName,
              projectName: item.ItemContent,
              state: '处理状态暂无',
              time: formatTime(item.NextRemindDate).format('HH:mm:ss')
            }
            tableKeepData.push(obj)
          })
          this.tableKeepData = tableKeepData
        } else {
          mes()
        }
        // 设备报警
        const data = !!nv && nv
        if (data) {
          const tableAlertData = new Array()
          const arr = data.AlarmList
          // this.countNum++
          arr.forEach((item, index) => {
            const obj = {
              content: index + 1 + '. ' + item.ProjectName ,
              id: item.GeneratorSetId,
              mchModel: item.GensetName,
              projectName: item.Content,
              state: '处理状态暂无',
              time: formatTime(item.Time).format('HH:mm:ss')
            }
            tableAlertData.push(obj)
          })
          this.tableAlertData = tableAlertData
        } else {
          mes()
        }
        // 环境风险
        const data2 = !!nv && nv
        if (data2) {
          const tableWeatherData = new Array()
          const arr = data2.WeatherWarnList
          arr.forEach((item, index) => {
            const obj = {
              content: index + 1 + '. ' + item.ProjectName,
              id: item.GeneratorSetId,
              mchModel: item.GensetName,
              projectName: item.Headline,
              state: '处理状态暂无',
              time: this.$moment(item.SendTime).format('HH:mm:ss')
            }
            tableWeatherData.push(obj)
          })
          this.tableWeatherData = tableWeatherData
        } else {
          mes()
        }
      },
      deep: true
    }
  },
  data() {
    return {
      alert: _data.alert,
      tableKeepColumn: _data.alert.tableKeep.column,
      tableKeepData: [],
      tableAlertData: [],
      tableWeatherData: [],
      // 计数器
      // countNum: 0
    }
  },
  methods: {
    diagnoseHandler(param) {
      this.$router.push('/project/project-monitor')
    }
  }
}
</script>

<style lang="less" scoped>
// 鼠标悬停表格行颜色
@li-hover-color: #183b58;
// 标题文字颜色
@title-text-color: #0099ff;
@color-border: #0099ff;
// 边框样式
.border-style {
  border: 1px solid @color-border;
}
.content-item {
  position: relative;
  &.content-item__left {
    margin-right: 8px;
  }
  .summary {
    position: absolute;
    top: 11px;
    right: 35px;
    font-size: 1.1rem;
    // 2022-3-25 11:24:13 处理状态暂时隐藏
    display: none;
    > span {
      color: #f59a33;
    }
  }
  // margin: 4px 2px 2px 2px;
  & > div:nth-child(1) {
    height: 40px;
    display: flex;
    .border-style();
    border-bottom: 0;
    font-size: 1.1rem;
    font-weight: bold;
    padding: 0 0 0 5px;
    align-items: center;
    justify-content: start;
    color: @title-text-color;
  }
  & > div:nth-child(2) {
    height: calc(100% - 40px);
  }
}
// table style
/deep/ .scroll-table-head {
  color: @title-text-color;
}
/deep/ .diagnosis {
  color: #228b22;
  &:hover {
    color: tomato;
  }
}
/deep/ .scroll-table {
  .border-style();
  box-sizing: border-box;
  .scroll-table-head {
    border-bottom: 1px solid #0a6fa8;
  }
  .scroll-table-body {
    li:hover {
      background: @li-hover-color;
    }
  }
}
.monitoring-screen-operation-bottom {
  height: 100%;
}
.el-row {
  height: 100%;
  > .el-col {
    // height: 350px;
    height: 100%;
    &:nth-child(1) {
      // padding: 0 20px 0 0;
    }
    &:nth-child(2) {
      padding: 0 20px;
    }
    &:nth-child(3) {
      // padding: 0 0 0 20px;
    }
  }
}
</style>



