<template>
  <el-row class="monitoring-screen-operation-top">
    <!-- 左侧项目总览和机组状态 -->
    <el-col :span="7">
      <el-row>
        <el-row>
          <div class="project-title">项目总览</div>
          <el-row class="mb16 project-data-box" type="flex" justify="space-between">
            <el-col :span="6">
              <div>{{ projectOverviewData[0] }}</div>
              <p>机组总数</p>
            </el-col>
            <el-col :span="6">
              <div>{{ projectOverviewData[1] }}</div>
              <p>运维机组数</p>
            </el-col>
            <el-col :span="6">
              <div>{{ projectOverviewData[2] }}</div>
              <p>代运维机组数</p>
            </el-col>
            <el-col :span="6">
              <div>{{ projectOverviewData[3] }}</div>
              <p>项目总数</p>
            </el-col>
          </el-row>
        </el-row>
        <el-row>
          <div class="project-title">机组状态</div>
          <el-row class="echarts">
            <e-charts
              :numArr="mchStateData.numArr"
              :numArrOperation="mchStateData.numArrOperation"
              :total="mchStateData.total"
            />
          </el-row>
        </el-row>
      </el-row>
    </el-col>
    <!-- 中部地图 -->
    <el-col :span="10" class="map">
      <el-row hidden class="map-query">
        <el-date-picker
          :popper-append-to-body="false"
          value-format="yyyy"
          v-model="year"
          type="year"
          placement="bottom-start"
        >
        </el-date-picker>
        <el-select v-model="rentingState" placeholder="请选择租赁状态">
          <el-option
            v-for="(item, index) in dropdownData.rentingStateOpt"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
        <el-select v-model="operationState" placeholder="请选择机组状态">
          <el-option
            v-for="(item, index) in dropdownData.operationStateOpt"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
        <el-button type="text" @click="resetQuery">重置筛选</el-button>
      </el-row>
      <e-charts-map v-if="mapType == 'china'" :mapData="mapData" />
      <map-baidu-word v-if="mapType == 'word'" :mapdata="mapData" />
    </el-col>
    <!-- 右侧项目架构和机组架构 -->
    <el-col :span="7">
      <!-- 项目架构 -->
      <el-row>
        <el-row class="mb16" type="flex" justify="space-between">
          <el-col :span="12">
            <div class="project-title">项目架构</div>
          </el-col>
          <div class="framework-detail-btn" @click="showFrameworkDetail">架构详情</div>
        </el-row>
        <div class="project-framework-box">
          <frame-work-tree :json="treeData" />
        </div>
        <el-dialog
          title="架构详情"
          class="framework-detail-dialog"
          :top="'10vh'"
          :center="true"
          :visible.sync="dialogVisible"
          :modal-append-to-body="false"
          :close-on-click-modal="false"
          :close-on-press-escape="false"
          @close="dialogVisible = false"
        >
          <div class="framework-detail-box">
            <FrameWorkDetailTree v-if="dialogVisible" :json="treeData"></FrameWorkDetailTree>
          </div>
        </el-dialog>
      </el-row>
      <!-- 客户反馈 -->
      <el-row>
        <div class="project-title">客户反馈（{{ feedback.total }}）</div>
        <el-row class="feedback-container" ref="feedbackContainer">
          <!-- <div ref="feedbackSub" title="点击查看详情" @click="checkDetail"> -->
          <div ref="feedbackSub" title="点击查看详情">
            <el-row
              v-for="(item, index) in feedback.items"
              :key="index"
              type="flex"
              align="middle"
              class="feedback-item"
            >
              <el-col :span="2">
                <el-avatar @error="handleError" :src="item.avatarUrl" icon="el-icon-user-solid">
                  <img src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png" />
                </el-avatar>
              </el-col>
              <el-col :span="22">
                <el-row justify="space-around">
                  <el-col :span="17" class="feedback-title-container">
                    <div class="feedback-type">
                      {{ feedback.itemType[item.itemType] }}
                    </div>
                    <div class="feedback-title">{{ item.title }}</div>
                  </el-col>
                  <el-col :span="7">
                    <el-row type="flex" justify="end">
                      {{ getFormatDateTime(item.dateTime) }}
                    </el-row>
                  </el-col>
                </el-row>
                <el-row class="feedback-content" @click.native="checkDetail">
                  {{ item.content }}
                  <span hidden>{{ item.avatarUrl }}</span>
                  <span hidden>{{ item.feedbackId }}</span>
                </el-row>
              </el-col>
            </el-row>
            <div v-if="!!feedback.items && feedback.items.length > 3">
              <el-row
                v-for="(item, index) in feedback.items"
                :key="index"
                type="flex"
                align="middle"
                class="feedback-item"
              >
                <el-col :span="2">
                  <el-avatar :src="item.avatarUrl" icon="el-icon-user-solid"></el-avatar>
                </el-col>
                <el-col :span="22">
                  <el-row justify="space-around">
                    <el-col :span="17" class="feedback-title-container">
                      <div class="feedback-type">
                        {{ feedback.itemType[item.itemType] }}
                      </div>
                      <div class="feedback-title">{{ item.title }}</div>
                    </el-col>
                    <el-col :span="7">
                      <el-row type="flex" justify="end">
                        {{ getFormatDateTime(item.dateTime) }}
                      </el-row>
                    </el-col>
                  </el-row>
                  <el-row class="feedback-content" @click.native="checkDetail">
                    {{ item.content }}
                    <span hidden>{{ item.avatarUrl }}</span>
                    <span hidden>{{ item.feedbackId }}</span>
                  </el-row>
                </el-col>
              </el-row>
            </div>
          </div>
        </el-row>
        <el-dialog
          title="客户反馈详情"
          class="feedback-detail-dialog"
          :visible.sync="dialogVisibleFeedback"
          width="40%"
          :before-close="handleClose"
          :append-to-body="true"
        >
          <el-row class="feedback-detail-container">
            <el-row class="feedback-detail-container-info">
              <div class="feedback-detail-container-info-title">
                <div>{{ feedbackDetail.Sort }}</div>
                <div>&nbsp;&nbsp;{{ feedbackDetail.FeedbackTitle }}</div>
              </div>
              <div class="feedback-detail-container-info-info">
                <el-avatar :src="feedbackThisHead" :size="20"></el-avatar>&nbsp;&nbsp;
                {{ feedbackDetail.CreateUserName }} {{ feedbackDetail.CreateUserCompanyName }}
                {{ feedbackDetail.CreateUserPhone }}
                {{ feedbackDetail.CreateTime }}
              </div>

              <div class="feedback-detail-container-info-content">
                {{ feedbackDetail.FeedbackDetail }}
              </div>
              <div
                v-if="!!feedbackDetail.FileUrlArray && feedbackDetail.FileUrlArray.length > 0"
                class="feedback-detail-container-info-pics"
              >
                <el-image
                  v-for="(e, i) in feedbackDetail.FileUrlArray"
                  :key="i"
                  :src="e"
                  fit="contain"
                  :preview-src-list="feedbackDetail.FileUrlArray"
                ></el-image>
              </div>
              <el-divider></el-divider>
            </el-row>
            <el-row class="feedback-detail-container-reply">
              <div
                v-for="(e, i) in feedbackDetail.ReplyList"
                :key="i"
                class="feedback-detail-container-reply-item"
              >
                <div>
                  <el-avatar
                    :key="e.FeedbackReplyId"
                    :src="handlePicUrl(e.CreateUserHeadImgUrl)"
                    :size="30"
                  ></el-avatar>
                </div>
                <div>
                  <div>
                    {{ e.CreateUserName }}
                    {{ formatTime(e.CreateTime).format('yyyy-MM-DD HH:mm:ss') }}
                  </div>
                  <div>
                    <el-input
                      resize="none"
                      v-if="!!e.ReplyDetail"
                      readonly
                      width="100%"
                      type="textarea"
                      :value="e.ReplyDetail"
                    >
                    </el-input>
                    <el-image
                      v-else
                      :src="handlePicUrl(e.FileUrl)"
                      :preview-src-list="feedbackDetail.replyPicList"
                      fit="contain"
                    ></el-image>
                  </div>
                </div>
              </div>
            </el-row>
            <el-divider></el-divider>
            <el-row class="feedback-detail-container-text">
              <el-col :span="19">
                <el-input
                  v-if="replyType == 'text'"
                  type="textarea"
                  placeholder="字数不超过100个字符"
                  :maxlength="100"
                  v-model="reply"
                  rows="4"
                >
                </el-input>
                <!-- <el-input v-else v-model="replyFiles" type="file"></el-input> -->
                <!-- <el-image v-if="replyType != 'text'"></el-image> -->
                <!-- 图片上传相关（本地预览） -->
                <el-upload
                  accept=".jpg, .jpeg, .png"
                  v-else
                  action="#"
                  list-type="picture-card"
                  :auto-upload="false"
                  :limit="picLimit"
                  :on-exceed="handleExceed"
                  :multiple="true"
                  :on-change="onChangePic"
                  ref="refUploadPics"
                >
                  <i slot="default" class="el-icon-plus"></i>
                  <div slot="file" slot-scope="{ file }">
                    <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
                    <span class="el-upload-list__item-actions">
                      <span
                        class="el-upload-list__item-preview"
                        @click="handlePictureCardPreview(file)"
                      >
                        <i class="el-icon-zoom-in"></i>
                      </span>
                      <!-- <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleDownload(file)"
                      >
                        <i class="el-icon-download"></i>
                      </span> -->
                      <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleRemove(file)"
                      >
                        <i class="el-icon-delete"></i>
                      </span>
                    </span>
                  </div>
                </el-upload>
                <el-dialog :modal-append-to-body="false" :visible.sync="dialogVisiblePic">
                  <img width="100%" :src="dialogImageUrl" alt="" />
                </el-dialog>
              </el-col>
              <el-col :span="4" :push="1">
                <el-radio v-model="replyType" label="text">文本</el-radio>
                <el-radio v-model="replyType" label="pic">图片</el-radio>
              </el-col>
            </el-row>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleFeedback = false">取 消</el-button>
            <el-button type="primary" @click="sendFeedbackReply">发 送</el-button>
          </span>
        </el-dialog>
      </el-row>
    </el-col>
  </el-row>
</template>
<script>
import _ from 'lodash'
import dayjs from 'dayjs'
import { mapActions, mapGetters } from 'vuex'

import ECharts from './ECharts.vue'
import EChartsMap from './EChartsMap.vue'
import MapBaiduWord from './MapBaiduWord.vue'
import FrameWorkDetailTree from './FrameWorkDetailTree.vue'
import FrameWorkTree from './frameworkTree.vue'

import _data from '../data'
import { formatTime, handlePicUrl } from '@/utils/common'

export default {
  name: 'MonitoringScreenOperationTop',
  components: {
    FrameWorkTree,
    FrameWorkDetailTree,
    EChartsMap,
    ECharts,
    MapBaiduWord
  },
  data() {
    return {
      // 架构详情弹窗可见性
      dialogVisible: false,
      // 项目总览数据 机组总数 运维机组数 代运维机组数 项目总数
      projectOverviewData: ['-', '-', '-', '-'],
      // 项目架构数据
      treeData: [],
      // 机组状态数据
      mchStateData: {
        total: 0,
        // 代租赁 租赁中 施工 检修 维修
        // 0 1 2 3 4
        numArr: [],
        // 运行 故障 风险 停机 离线
        // 0 1 2 3 4
        numArrOperation: []
      },
      // 下拉框选项数据
      dropdownData: {
        rentingStateOpt: [],
        operationStateOpt: []
      },
      // 客户反馈数据
      feedback: {
        total: 0,
        items: []
      },
      // 筛选条件：年、租赁状态、机组状态
      year: new Date().getFullYear().toString(),
      rentingState: null,
      operationState: null,
      // 客户反馈滚动数据状态
      timer: null,
      speed: 100,
      mapData: {},
      mapDataAll: {},
      // 地图类型
      // mapType: 'china'
      mapType: 'word',
      dialogVisibleFeedback: false,
      // 反馈详情
      feedbackDetail: {},
      reply: '',
      replyType: 'text',
      replyFiles: [],
      feedbackId: 0,
      feedbackThisHead: '',
      // 图片上传相关
      dialogImageUrl: '',
      dialogVisiblePic: false,
      disabled: false,
      picLimit: 3,
      // 图片大小限制 5M
      picSizeLimit: 5 * 1024 * 1024,
      // 反馈列表滚动 top
      top: 0
    }
  },
  props: {
    allData: {
      type: Object,
      default: (_) => {
        return {}
      }
    }
  },
  inject: ['mes'],
  provide: {
    // 运行 故障 风险 停机 离线
    colorOperation: ['#20be62', '#f59a23', '#8ec9f4', '#d9001b', '#aaaaaa']
  },
  computed: {
    ...mapGetters(['monitoringScreenOperation'])
  },
  watch: {
    allData: {
      handler: function (nv, ov) {
        if (!!nv) {
          // 项目总览
          const projectOverviewData = [
            nv.GensetAll,
            nv.GensetAllSelf,
            nv.GensetAllOM,
            nv.ProjectAll
          ]
          this.projectOverviewData = projectOverviewData
          // 机组状态
          const dropdownData = this.dropdownData
          const mchStateData = {
            total: 0,
            numArr: [],
            numArrOperation: [],
            // 0 检修 1 租赁中 2 施工 3 待租赁 4 维修
            mchStateMapping: [
              'GensetInfoStatus_4Count',
              'GensetInfoStatus_1Count',
              'GensetInfoStatus_3Count',
              'GensetInfoStatus_0Count',
              'GensetInfoStatus_2Count'
            ],
            // 0 运行 1 故障 2 风险 3 停机 4 离线
            operationSMapping: [
              'GensetStatusRun',
              'GensetStatusAlarm',
              'GensetStatusWarn',
              'GensetStatusStop',
              'GensetStatusOffline'
            ]
          }
          mchStateData.mchStateMapping.forEach((item, index) => {
            const obj = {
              name: dropdownData.rentingStateOpt[index],
              value: nv[item]
            }
            mchStateData.numArr.push(obj)
            mchStateData.total += nv[item]
          })
          mchStateData.operationSMapping.forEach((item, index) => {
            const obj = {
              name: dropdownData.operationStateOpt[index],
              value: nv[item]
            }
            mchStateData.numArrOperation.push(obj)
          })
          this.mchStateData = mchStateData
          // 机组分布
          const mes = this.mes
          this.$api.monitoringScreenOperation
            .FindGensetProjectIndexMap(
              {},
              {
                GeneratorSetName: '',
                CollectorToken: '',
                Status: -1,
                RunStatus: -1
              }
            )
            .then((res) => {
              const mapDataOriginal = !!res && !!res.Data && res.Data
              if (mapDataOriginal) {
                let mapData = new Object()
                if (mapDataOriginal.length > 0) {
                  mapDataOriginal.forEach((item) => {
                    const arr = [
                      item.Longitude,
                      item.Latitude,
                      item.InstallationSite,
                      item.GeneratorSetId,
                      '项目类型',
                      item.ProjectName,
                      '是否重点机组标志',
                      item.DisplayName,
                      '项目合同编号',
                      '服务企业名称',
                      '项目合同编号',
                      '服务企业编号',
                      item.RunStatus,
                      item.InfoStatus
                    ]
                    mapData[item.GeneratorSetId] = arr
                  })
                  this.mapData = mapData
                }
              } else {
                mes()
              }
            })
            .catch((err) => {
              mes()
              console.error(`ERROR in monitoringScreenOperation get map data =>`, err)
            })
          // 项目架构
          const treeData = {
            id: 0,
            name: '智能装备动力服务租赁平台',
            level: 0,
            children: []
          }
          const projectList = nv.ProjectList
          if (!!projectList) {
            projectList.forEach((item) => {
              const obj = {
                id: item.ProjectId,
                name: item.ProjectName,
                type: '单机项目',
                level: 2,
                data: []
              }
              treeData.children.push(obj)
            })
            if (!this.dialogVisible) this.treeData = treeData
          }
          // 客户反馈
          this.$api.monitoringScreenOperation
            .FindGensetProjectIndexFeedback()
            .then((res) => {
              const feedbackData = !!res && !!res.Data && res.Data
              if (feedbackData) {
                const feedback = new Object()
                const items = new Array()
                feedback.total = feedbackData.FeedbackCount || 0
                feedback.itemType = ['项目建议', '投诉', '投诉类型']
                if (feedbackData.FeedbackList && feedbackData.FeedbackList.length > 0)
                  feedbackData.FeedbackList.forEach((item, index) => {
                    const obj = {
                      avatarUrl: this.handlePicUrl(item.CreateUserHeadImgUrl),
                      itemType: item.Sort,
                      title: index + 1 + '. ' + item.FeedbackTitle,
                      dateTime: formatTime(item.CreateTime).format('yyyy-MM-DD HH:mm:ss'),
                      content: item.FeedbackDetail,
                      feedbackId: item.FeedbackId
                    }
                    items.push(obj)
                  })
                feedback.items = items
                this.feedback = feedback
              } else {
                this.mes()
              }
            })
            .catch((err) => {
              mes()
              console.error(`ERROR in monitoringScreenOperation get feedback data =>`, err)
            })
          // 用户在打开了机组架构的情况下进行刷新会无法获取数据
          this.getMchTree()
        }
      },
      deep: true
    },
    year() {
      this.toggleMSO()
    },
    rentingState(v) {
      this.toggleMSO()
    },
    operationState() {
      this.toggleMSO()
    },
    monitoringScreenOperation: {
      handler: function (nv, ov) {
        const { year, operationState, rentingState } = nv
        const mapData = {}
        // 根据筛选条件更新地图数据 12 运行状态 13 租赁状态
        for (const ky in this.mapDataAll) {
          const arr = [...this.mapDataAll[ky]]
          const item = this.mapDataAll[ky]
          if (operationState !== null && rentingState !== null) {
            if (operationState == item[12] && rentingState == item[13]) mapData[ky] = arr
          } else if (operationState === null && rentingState !== null) {
            // debugger
            if (rentingState == item[13]) {
              mapData[ky] = arr
            }
          } else if (operationState !== null && rentingState === null) {
            if (operationState == item[12]) mapData[ky] = arr
          } else {
            mapData[ky] = arr
          }
        }
        // this.mapData = mapData
      },
      deep: true
      // immediate: true
    }
  },
  methods: {
    // *** services
    /**
     * 格式化客户反馈数据项目日期时间
     * @param dateTime 原始时间
     * @returns 前天|昨天|今天|日期 + 时间（分钟）
     */
    getFormatDateTime(dateTime) {
      const date = dayjs(dateTime)
      const diff = date.diff(dayjs(), 'd')
      const time = date.format('HH:mm')
      let result = dateTime
      switch (diff) {
        case 0:
          result = '今天' + time
          break
        case -1:
          result = '昨天' + time
          break
        case -2:
          result = '前天' + time
          break
        default:
          result = date.format('M月D日HH:mm')
          break
      }
      return result
    },
    showFrameworkDetail() {
      this.getMchTree()
      this.dialogVisible = true
    },
    // 获取项目机组架构
    getMchTree() {
      if (!this.dialogVisible) {
        this.$api.monitoringScreenOperation
          .FindGensetProjectIndexProjectTree()
          .then((res) => {
            const data = !!res && !!res.Data && res.Data
            if (data) {
              const projectListMap = new Map()
              const projectList = data.ProjectList
              const children = _.cloneDeep(this.treeData.children)
              projectList.forEach((item) => {
                projectListMap.set(item.ProjectId, item)
              })
              children.forEach((item0) => {
                const projectItem = projectListMap.get(item0.id)
                const gensetList = projectItem.GensetList
                const aData = []
                if (gensetList.length > 0) {
                  gensetList.forEach((item) => {
                    const obj = {
                      ...item
                    }
                    aData.push(obj)
                  })
                  item0.data = aData
                  item0.GensetOffline = projectItem.GensetOffline
                  item0.GensetRun = projectItem.GensetRun
                  item0.GensetStop = projectItem.GensetStop
                  item0.GensetWarn = projectItem.GensetWarn
                  item0.GensetAlarm = projectItem.GensetAlarm
                  item0.GensetAll = projectItem.GensetAll
                }
              })
              this.treeData.children = children
            } else {
              this.mes()
            }
          })
          .catch((err) => {
            this.mes()
            console.error(`ERROR in monitoringScreenOperation get mch tree data =>`, err)
          })
      }
    },
    ...mapActions(['toggleMonitoringScreenOperation']),
    // *** functional
    // 获取下拉框选择数据
    getDropdownData() {
      const dropdownData = {
        rentingStateOpt: ['检修', '租赁中', '施工', '待租赁', '维修'],
        operationStateOpt: ['运行', '故障', '风险', '停机', '离线']
      }
      this.dropdownData = dropdownData
    },
    // 初始化数据
    initData() {
      // const dropdownData = this.dropdownData
      // const mchStateData = {
      //   total: 0,
      //   numArr: [],
      //   numArrOperation: []
      // }
      // _data.mchStateData.numArr.forEach((item, index) => {
      //   const obj = {
      //     name: dropdownData.rentingStateOpt[index],
      //     value: item
      //   }
      //   mchStateData.total += item
      //   mchStateData.numArr.push(obj)
      // })
      // _data.mchStateData.numArrOperation.forEach((item, index) => {
      //   const obj = {
      //     name: dropdownData.operationStateOpt[index],
      //     value: item
      //   }
      //   mchStateData.numArrOperation.push(obj)
      // })
      // this.mchStateData = mchStateData
      // this.treeData = _data.treeData
      // this.projectOverviewData = _data.projectOverviewData
      // this.feedback = _data.feedback
      // this.feedback.items.map((item) => {
      //   item.content.length > 135 && (item.content = item.content.slice(0, 135) + '...')
      // })
      this.toggleMSO()
    },
    // 客户反馈内容自动滚动
    feedbackBeginScroll() {
      const d = {
        containerEl: this.$refs.feedbackContainer.$el,
        containerHeight: this.$refs.feedbackContainer.$el.clientHeight,
        innerEl: this.$refs.feedbackSub,
        innerContainerH: this.$refs.feedbackSub.clientHeight
      }
      if (d.containerHeight < d.innerContainerH) {
        const $html = d.innerEl.innerHTML
        // d.innerEl.innerHTML = $html + $html
        this.timer = setInterval(() => {
          if (this.top >= d.innerContainerH / 2) {
            this.top = 0
          } else {
            this.top++
          }
          d.innerEl.style.top = `-${this.top}px`
        }, this.speed)
        // this.time = setInterval(() => {
        //   const sti=d.innerEl
        //   const child = sti.firstChild
        //   const childClone = child.cloneNode(true)
        //   child.style.transition = 'height 1.4s .5s,color .5s'
        //   child.style.height = 0
        //   child.style.color = 'transparent'
        //   function _change() {
        //     sti.lastChild.after(childClone)
        //     child.remove()
        //   }
        //   child.addEventListener('transitionend', _change)
        // }, 1000)
      }
    },
    // 客户反馈查看详情弹窗
    checkDetail(e) {
      if (!!e) {
        this.dialogVisibleFeedback = true
        const el = e.target
        const feedbackThisHead = el.children[0].innerHTML
        const feedbackId = Number(el.lastChild.innerText)

        this.feedbackId = feedbackId
        this.feedbackThisHead = feedbackThisHead
      }
      this.$api.monitoringScreenOperation
        .GetGensetProjectFeedback({}, { id: this.feedbackId })
        .then((res) => {
          const data = !!res && !!res.Data && res.Data
          if (data) {
            const feedbackDetail = { ...data }
            const replyPicList = []
            !!feedbackDetail.ReplyList &&
              feedbackDetail.ReplyList.forEach((e) => {
                !!e.FileUrl && !e.ReplyDetail && replyPicList.push(this.handlePicUrl(e.FileUrl))
              })
            !!feedbackDetail.FileUrlArray &&
              (feedbackDetail.FileUrlArray = feedbackDetail.FileUrlArray.map((e) => {
                return this.handlePicUrl(e)
              }))
            feedbackDetail.replyPicList = replyPicList

            this.feedbackDetail = feedbackDetail
            !!this.feedbackDetail.CreateTime &&
              (this.feedbackDetail.CreateTime = formatTime(this.feedbackDetail.CreateTime).format(
                'yyyy-MM-DD HH:mm:ss'
              ))
          } else {
            this.mes()
          }
        })
        .catch((err) => {
          this.mes()
          console.error(`ERROR in monitoringScreenOperation get feedback detail data =>`, err)
        })
    },
    handleError(e) {
      return true
    },
    // 处理图片URL
    handlePicUrl(url) {
      const res = `${process.env.BASE_API}/${url}`

      return res
    },
    // 处理图片（用户信息在老系统）
    handlePicUrlOld(url) {
      const res = `http://www.fortrustyun.com/${url}`

      return res
    },
    // 反馈详情弹窗关闭事件
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then((_) => {
          done()
        })
        .catch((_) => {})
    },
    // 反馈详情回复发送
    sendFeedbackReply() {
      const message = this.$message
      const upload = (!!this.$refs.refUploadPics && this.$refs.refUploadPics) || {
        uploadFiles: []
      }
      const uploadFiles = upload.uploadFiles
      if (this.replyType == 'text' && !this.reply) {
        message({
          message: '请输入回复信息！',
          type: 'error'
        })
        return
      } else if (this.replyType != 'text' && uploadFiles.length === 0) {
        message({
          message: '至少上传一张图片！',
          type: 'error'
        })
        return
      }
      this.$confirm('确认发送？')
        .then(() => {
          const loading = this.$loading({
            lock: true,
            text: '数据提交中，请稍候...',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
          const params = new FormData()
          params.append('FeedbackId', this.feedbackId)
          params.append('ReplyDetail', this.reply)
          params.append('FileUrl', '')
          // 备注暂无
          params.append('Remark', '')
          const promiseArr = []
          if (uploadFiles.length > 0) {
            uploadFiles.forEach((e, i) => {
              const params = new FormData()
              params.append('FeedbackId', this.feedbackId)
              params.append('ReplyDetail', this.reply)
              params.append('FileUrl', e.raw)
              params.append('Remark', '')
              promiseArr.push(this.sendFeedbackReplyFunc(params))
            })
          } else {
            promiseArr.push(this.sendFeedbackReplyFunc(params))
          }
          Promise.all(promiseArr).then((data) => {
            message({
              message: `发送成功！`,
              type: 'success'
            })
            loading.close()
            // 不关闭
            // this.dialogVisibleFeedback = false
            this.reply = ''
            uploadFiles.length = 0
            this.checkDetail()
          })
          // ffffffffffffffffffffffffff
          // if (responseAll) {
          //   message({
          //     message: `发送成功！`,
          //     type: 'success'
          //   })
          //   loading.close()
          //   // 不关闭
          //   // this.dialogVisibleFeedback = false
          //   this.reply = ''
          //   this.checkDetail()
          // } else {
          //   this.mes()
          //   loading.close()
          //   if (errorIndex.length > 0) {
          //     this.$message('图片上传失败，请重新上传！')
          //   }
          // }
        })
        .catch((_) => {})
    },
    // 调用反馈回复接口（单次仅可发送一个图片）
    sendFeedbackReplyFunc(params) {
      return new Promise((resolve, reject) => {
        this.$api.monitoringScreenOperation.AddGGensetProjectFeedbackReply(params).then((res) => {
          if (res.Code === 1) {
            resolve('success')
          } else {
            reject(res)
          }
        })
      })
    },
    // 项目地图切换筛选条件
    toggleMSO() {
      const monitoringScreenOperation = {
        year: this.year,
        rentingState: this.rentingState,
        operationState: this.operationState
      }
      this.toggleMonitoringScreenOperation(monitoringScreenOperation)
    },
    // 重置筛选
    resetQuery() {
      this.year = new Date().getFullYear().toString()
      this.rentingState = null
      this.operationState = null
      this.$message({ message: '重置成功！', type: 'success', duration: 1000, showClose: true })
    },
    formatTime(str) {
      return formatTime(str)
    },
    // 图片上传相关
    handleRemove(file) {
      // upload 对象
      const uid = file.uid
      const upload = this.$refs.refUploadPics
      const uploadFiles = upload.uploadFiles
      try {
        uploadFiles.forEach((e, i) => {
          if (e.uid === uid) uploadFiles.splice(i, 1)
        })
      } catch (error) {
        this.$message({
          type: 'error',
          message: '删除失败！'
        })
        console.error(error)
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisiblePic = true
    },
    handleDownload(file) {},
    handleExceed() {
      this.$message(`仅限上传${this.picLimit}张图片！`)
    },
    onChangePic(file, fileList) {
      const isLt5M = file.size / 1024 <= 5120
      if (!isLt5M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 5MB!')
        return false
      }
    }
  },
  created() {
    const mapData = {}
    for (const ky in _data.mapData) {
      if (_data.mapData[ky][11] === 'A') {
        // 运行状态
        _data.mapData[ky].push(Number((Math.random() * 3).toFixed(0)))
        // 租赁状态
        _data.mapData[ky].push(Number((Math.random() * 4).toFixed(0)))
        mapData[ky] = _data.mapData[ky]
      }
    }
    this.mapData = mapData
    this.mapDataAll = mapData
  },
  mounted() {
    this.getDropdownData()
    this.initData()
  },
  updated() {
    clearInterval(this.timer)
    this.feedbackBeginScroll()
  },
  destroyed() {
    clearInterval(this.timer)
  }
}
</script>
<style lang="less" scoped>
@color-border: #0099ff;
.monitoring-screen-operation-top {
  height: 100%;
  & > .el-col {
    height: 100%;
    &:nth-child(1) {
      & > .el-row {
        height: 100%;
        > .el-row:nth-child(1) {
          height: 135px;
        }
        > .el-row:nth-child(2) {
          height: calc(100% - 135px);
        }
      }
    }
  }
}
.project-data-box,
.crew-data-box {
  border: 2px solid @color-border;
  border-radius: 4px;
  .el-col {
    line-height: 28px;
    text-align: center;
    padding: 10px 0;
    &:not(:last-child) {
      border-right: 2px solid rgba(0, 204, 255, 1);
    }
    div {
      font-size: 28px;
      font-weight: bold;
      margin-bottom: 8px;
    }
  }
}
.el-row.mb16 {
  margin-bottom: 16px;
}
/deep/ .project-framework-box {
  height: 42%;
  margin-bottom: 16px;
  overflow-x: hidden;
  &:hover {
    overflow-x: auto;
    overflow-x: overlay;
  }
  > div > table {
    padding: 0 16px;
  }
}
.framework-detail-dialog {
  background: transparent;
  .framework-detail-box {
    height: calc(100% - 110px);
    overflow-x: hidden;
    padding-bottom: 12px;
    &:hover {
      overflow-x: auto;
    }
  }
  /deep/ .el-dialog {
    width: 80%;
    height: 80%;
    background: linear-gradient(
      180deg,
      rgba(10, 22, 46, 0.8) 0%,
      rgba(16, 36, 71, 0.8) 40%,
      rgba(16, 36, 71, 0.8) 61%,
      rgba(8, 16, 37, 0.8) 100%
    );
    .el-dialog__header {
      border-bottom: 1px solid #1acdfd;
      .el-dialog__title {
        color: #b4e4ff;
        font-weight: 600;
      }
      .el-dialog__headerbtn .el-dialog__close {
        font-size: 24px;
        color: #1acdfd;
      }
    }
    .el-dialog__body {
      color: #1acdfd;
    }
  }
}
.framework-detail-btn {
  color: #1acdfd;
  cursor: pointer;
  line-height: 20px;
  position: absolute;
  right: 0;
  top: 10px;
}
.map {
  padding: 46px 20px 0 20px;
  height: 100%;
  &-query {
    position: absolute;
    top: 46px;
    right: 33%;
    padding: 0 10px;
    z-index: 9;
    /deep/input {
      background: #0c1c39;
      border: 1px solid @color-border;
      color: white;
    }
    /deep/ .el-button--text {
      color: #1acdfd;
    }
    > .el-input:first-child {
      width: 100px;
    }
  }
}
.echarts {
  height: calc(100% - 50px);
}
// ***
// 客户反馈样式
.feedback-container {
  border: 1px solid @color-border;
  // padding: 5px;
  height: 374px;
  overflow: hidden;
  > div:first-child {
    position: absolute;
    width: 100%;
  }
  .feedback-item {
    border: 1px solid @color-border;
    border-radius: 10px;
    padding: 5px;
    &:nth-child(n + 1) {
      margin: 10px 10px 10px 10px;
    }
    .feedback-title-container {
      display: flex;
    }
    .feedback-type {
      border: 1px solid @color-border;
      color: #09a4f1;
      padding: 0 4px;
    }
    .feedback-title {
      margin: 0 0 0 7px;
      font-weight: bold;
    }
    .feedback-content {
      padding: 10px 0 0 0;
      font-size: 13px;
      // font-size: 0.7rem;
      cursor: pointer;
      height: 60px;
      // 省略
      width: 100%;
      overflow: hidden;
      display: -webkit-box; /*弹性伸缩盒子*/
      -webkit-box-orient: vertical; /*子元素垂直排列*/
      -webkit-line-clamp: 3; /*可以显示的行数，超出部分用...表示*/
      text-overflow: ellipsis; /*（多行文本的情况下，用省略号“…”隐藏溢出范围的文本)*/
    }
  }
}
.feedback-detail-dialog {
  /deep/ .el-upload--picture-card {
    background-color: transparent;
  }
  /deep/ .el-upload-list--picture-card .el-upload-list__item {
    background-color: transparent;
  }
  /deep/ .el-dialog {
    background: linear-gradient(
      180deg,
      rgba(10, 22, 46, 1) 0%,
      rgba(16, 36, 71, 1) 40%,
      rgba(16, 36, 71, 1) 61%,
      rgba(8, 16, 37, 1) 100%
    );
    .el-dialog__header {
      border-bottom: 1px solid #06264a;
      text-align: center;
      .el-dialog__title {
        color: #b4e4ff;
        font-weight: 600;
      }
      .el-dialog__headerbtn .el-dialog__close {
        font-size: 24px;
        color: #1acdfd;
      }
    }
    .el-dialog__body {
      color: white;
    }
    .el-dialog__footer {
      padding: 0 20px 15px 0;
      border: none;
    }
  }
}
.feedback-detail-container {
  /deep/ .el-divider--horizontal {
    margin: 8px 0;
    background: #0896dd;
  }
  /deep/.el-textarea__inner {
    outline: 0;
    border-color: #409eff;
  }
  .el-image {
    height: 70px;
    width: 70px;
    &:not(:first-child) {
      margin: 0 0 0 10px;
    }
  }
  &-info {
    &-title {
      display: flex;
      align-items: center;
      & > div:first-child {
        border: 1px solid #0099ff;
        color: #09a4f1;
        padding: 0 4px;
        font-size: 0.8rem;
      }
      & > div:last-child {
        font-weight: bold;
        font-size: 1.3rem;
      }
    }
    &-info {
      display: flex;
      align-items: center;
      color: #eee;
      font-size: 0.8rem;
    }
    &-content {
      font-size: 1rem;
    }
  }

  &-reply {
    max-height: 250px;
    padding: 5px 0;
    overflow: auto;
    &-item {
      display: flex;
      // margin: 0 0 5px 0;
      & > div:first-child {
        margin: 0 10px 0 10px;
      }
      & > div:last-child {
        & :first-child {
          display: flex;
          align-items: center;
          color: #eee;
          font-size: 0.8rem;
          margin: 2px 0;
        }
        flex: 1;
        padding: 0 20px 0 0;
      }
    }
  }

  &-text {
    span[aria-hidden] {
      color: #818da5;
    }
    span {
      font-weight: bold;
    }
  }

  &-row {
    display: flex;
    padding: 0 0 20px 0;
    div:nth-child(1) {
      padding: 0 10px 0 0;
      display: flex;
      justify-content: end;
    }
    div:ntd-child(2) {
      width: calc(100% - 80px);
    }
  }
  /deep/ textarea {
    background: #0d1f42;
    color: white;
  }
}
// 架构详情按钮
/deep/.framework-detail-btn {
  top: 20px;
}
</style>



