<!--
 * @Author Demon3443002624@qq.com
 * @Date 2022-3-1 14:38:03
 * @LastEditors: linxu
 * @LastEditTime: 2022-05-12 16:18:38
 * @LastEditTime 2022-4-27 16:10:16
 * @Description 运维监控大屏
 * @Description 状态：
 * 1.机组状态 0 检修 1 租赁中 2 施工 3 待租赁 4 维修
 * 2.运行状态 0 运行 1 故障 2 风险 3 停机 4 离线
 * @Description 地图数据：
 * {机组编号:[经度、纬度、地址、机组编号、项目类型、
 * 所属项目、是否重点机组标志、机组名称、项目合同编码、服务企业名称、
 * 项目合同编码、服务企业编号、运行状态、租赁状态]}
-->
<template>
  <div class="employee-monitoring-screen monitoring-full-screen">
    <!-- :style="{
        transform: `scale(${scale})`,
        width: `${width}px`,
        height: `${height}px`
      }" -->
    <div class="ee-screen-content">
      <!-- 顶部 -->
      <el-header class="ee-screen-header">
        <div class="screen-header-left">
          <svg-icon icon-class="Icon-ee-header-left1"></svg-icon>
          <el-row type="flex">
            <el-col>
              <div class="city-button">
                <i class="el-icon-location-information"> </i>
                <span class="city-span">-</span>
              </div>
            </el-col>
            <el-col>
              <div class="nowTime">
                <p>
                  <span>{{ dateStr }}</span>
                </p>
                <p>
                  <span>{{ timeStr }}</span>
                </p>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="screen-header-center">
          <el-row type="flex">
            <el-col>
              <h2>智能装备动力服务租赁平台</h2>
            </el-col>
          </el-row>
        </div>
        <div class="screen-header-right">
          <svg-icon icon-class="Icon-ee-header-right1"></svg-icon>
          <el-row type="flex" justify="center">
            <el-col style="margin-left: 16px">
              <el-row type="flex" align="middle">
                <el-avatar :src="HeadImgUrl"> </el-avatar>
                &nbsp;&nbsp;{{ this.$wsCache.get('userInfo').displayName }}，您好！
              </el-row>
            </el-col>
            <el-col>
              <el-row class="btns">
                <el-col>
                  <el-button
                    :style="{
                      display: 'none'
                    }"
                    class="goto-system-btn enter-operation"
                    @click="pushOperation"
                  >
                    运维管理
                  </el-button>
                </el-col>
                <el-col>
                  <el-button class="goto-system-btn enter-manage" @click="pushManage">
                    管理系统
                  </el-button>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </el-header>
      <!-- 内容布局 -->
      <el-main class="ee-screen-main">
        <el-row><layout-top :allData="allData" /></el-row>
        <el-row><layout-bottom :allData="allData" /></el-row>
      </el-main>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import LayoutTop from './components/LayoutTop.vue'
import LayoutBottom from './components/LayoutBottom.vue'

import { getWeekTime, handlePicUrl } from '@/utils/common'

export default {
  name: 'MonitoringScreenOperation',
  components: {
    LayoutTop,
    LayoutBottom
  },
  data() {
    return {
      width: 1920,
      height: 1080,
      scale: 1,
      HeadImgUrl: '',
      dateStr: '',
      timeStr: '',
      allData: {},
      timer: null,
      // 页面刷新间隔 10秒
      interval: 20 * 1000
    }
  },
  methods: {
    getScale() {
      // 固定好16:9的宽高比，计算出最合适的缩放比，宽高比可根据需要自行更改
      const { width = 1920, height = 1080 } = this.$data
      let ww = window.innerWidth / width
      let wh = window.innerHeight / height
      return ww < wh ? ww : wh
    },
    setScale: _.debounce(function () {
      // 获取到缩放比，并设置
      this.scale = this.getScale()
    }, 300),
    getAccount() {
      this.$api.account.findAccount().then((res) => {
        if (res.IsSuccess) {
          const data = res.Data
          // if data.HeadImgUrl = null,then this.HeadImgUrl
          this.HeadImgUrl = data.HeadImgUrl && (data.HeadImgUrl = handlePicUrl(data.HeadImgUrl))
        }
      })
    },
    pushManage() {
      this.$router.push('/operation/operationStatistics') // /syshomePage/home
    },
    // 获取数据失败提示
    mes() {
      this.$message({
        message: '从服务器获取数据失败，请检查网络！',
        type: 'warning'
      })
    },
    pushOperation() {
      this.$message({
        message: '敬请期待！',
        type: 'success',
        duration: 1000,
        showClose: true
      })
    },
    renderTime() {
      const format = `YYYY年M月D日 星期 HH:mm`
      let str = getWeekTime(format)
      this.dateStr = str.split(' ')[0]
      this.timeStr = `${str.split(' ')[1]} ${str.split(' ')[2]}`
      setTimeout(() => {
        this.renderTime()
      }, 60 * 1000)
    },
    getData() {
      this.$api.monitoringScreenOperation
        .FindGensetProjectIndex()
        .then((res) => {
          const allData = !!res && !!res.Data && res.Data
          if (allData) {
            this.allData = allData
          } else {
            this.mes()
          }
        })
        .catch((err) => {
          this.mes()
          console.error(`ERROR in monitoringScreenOperation get all data =>`, err)
        })
    },
    // 封装定时器
    mySetInterval(fn, interval) {
      fn()
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.mySetInterval(fn, interval)
      }, interval)
    }
  },
  provide() {
    return {
      mes: this.mes
    }
  },
  created() {
    this.setScale()
  },
  beforeMount() {
    window.addEventListener('resize', this.setScale)
  },
  mounted() {
    this.getAccount()
    this.renderTime()
    this.getData()
    // this.mySetInterval(this.getData,this.interval)
    this.timer = setInterval(() => {
      console.info('refresh...')
      this.getData()
    }, this.interval)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.setScale)
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
// 页面框架布局
.employee-monitoring-screen {
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(10, 22, 46, 1) 0%,
    rgba(16, 36, 71, 1) 40%,
    rgba(16, 36, 71, 1) 61%,
    rgba(8, 16, 37, 1) 100%
  );
  color: #fff;
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei',
    '微软雅黑', Arial, sans-serif;
  // .ee-screen-content {
  // transform-origin: 0 0;
  // position: absolute;
  // // left: 50%;
  // top: 0;
  // transition: 0.3s;
  // }
  .ee-screen-content {
    // 必须显示申明高度
    height: 100%;
  }
  .ee-screen-header {
    padding: 0;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    > div {
      width: 25%;
      height: 100%;
      &.screen-header-center {
        width: 50%;
      }
    }
  }
  .ee-screen-main {
    padding: 24px 16px 16px;
    height: calc(100% - 60px);
    box-sizing: border-box;
    justify-content: space-between;
    > div:first-child {
      height: 65%;
      padding: 0 0 10px 0;
    }
    > div:last-child {
      height: 35%;
    }
  }
}
.ee-screen-header {
  color: #1acdfd;
  .screen-header-left {
    position: relative;
    padding-left: 16px;
    box-sizing: border-box;
    background: linear-gradient(to top, #154964 8%, #0a1b33 50%, transparent 100%) left top
      no-repeat;
    .svg-icon {
      position: absolute;
      right: -65px;
      width: 55%;
      height: 60px;
      z-index: 1;
      opacity: 0.3;
    }
    .city-button {
      padding: 8px 0;
      border: 0;
      background-color: transparent;
      color: #1acdfd;
      font-size: 1.3rem;
      line-height: 44px;
    }
    .nowTime {
      text-align: right;
      margin-right: 16px;
      padding: 6px 0;
      p {
        line-height: 24px;
      }
    }
  }
  .screen-header-center {
    h2 {
      margin: 0 16px;
      font-size: 40px;
      text-align: center;
      line-height: 80px;
      background-image: linear-gradient(#0a1b33, #154964);
      border-radius: 0 0 68px 68px;
    }
  }
  .screen-header-right {
    position: relative;
    padding-right: 16px;
    box-sizing: border-box;
    background: linear-gradient(to top, #154964 8%, #0a1b33 50%, transparent 100%) left top
      no-repeat;
    .svg-icon {
      position: absolute;
      left: -65px;
      width: 55%;
      height: 60px;
      z-index: 1;
      opacity: 0.3;
    }
    .el-row {
      height: 100%;
    }
    .btns {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    .goto-system-btn {
      margin-left: 16px;
      background: transparent;
      color: #fff;
      border-color: #00ccff;
      &:hover {
        background-color: rgba(26, 205, 253, 0.8);
        // -webkit-animation: oto-system-btn 0.5s linear 1;
        // animation: oto-system-btn 0.5s linear 1;
      }
    }
  }
  .icon-center {
    width: 100%;
    height: 80px;
  }
}

.left-content,
.center-content,
.right-content {
  display: flex;
  flex-direction: column;
  /deep/.content-item {
    width: 100%;
    padding: 6px;
    box-sizing: border-box;
    background: linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) left top no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) left top no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) right top no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) right top no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) left bottom no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) left bottom no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) right bottom no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) right bottom no-repeat;
    /*设置大小*/
    background-size: 1px 20px, 20px 1px, 1px 20px, 20px 1px, 1px 20px, 20px 1px, 1px 20px, 20px 1px;
  }
}
// 项目标题 样式
/deep/ .project-title {
  border-left: 4px solid #20fb8e;
  padding-left: 8px;
  font-size: 20px;
  font-weight: bold;
  line-height: 20px;
  margin: 15px 0;
}
</style>



