<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2022-3-1 14:38:03
 * @LastEditors: Demon3443002624@qq.com
 * @LastEditTime: 2022-3-1 14:38:32
 * @Description: 运维监控大屏组件-项目机组地图（ECharts）
-->
<template>
  <div class="map-component">
    <div class="map-component-map" ref="myEChart"></div>
    <el-card class="map-component-card box-card">
      <el-row class="title">机组租赁状态：</el-row>
      <el-row>
        <el-col v-for="(item, index) in legend.rentState" :key="index" :span="8">
          <svg-icon :icon-class="item.icon"></svg-icon>{{ item.name }}
        </el-col>
      </el-row>
      <!-- 2022-3-4 16:58:04 Demon 以饼图颜色标注即可 -->
      <!-- <el-row class="title">机组运行状态：</el-row> -->
      <!-- <el-row>
        <el-col
          v-for="(item, index) in legend.operatingState"
          :key="index"
          class="container"
          :span="8"
        >
          <div :style="{ backgroundColor: item.color }" class="color-box"></div>
          {{ item.name }}
        </el-col>
      </el-row> -->
    </el-card>
    <el-button
      @click="backHandler"
      title="恢复地图原始状态"
      class="map-component-reset"
      v-show="backBtnShow"
      icon="el-icon-refresh-left"
    ></el-button>
  </div>
</template>
<script>
// 引入测试数据
import _data from '../data'
import _svgPath from './svgPath'
import _mixin from './EchartsMap.js'
import NumberBox from '@/components/NumberBox/index.vue'

export default {
  name: `EChartChinaMap`,
  components: {
    NumberBox
  },
  data() {
    return {
      ..._mixin.data,
      // 地图背景颜色
      bakColorMap: `#193b69`,
      // 汇总图例  0、1、2
      cardData: [0, 0, 0],
      // 机组总数、项目总数、合作伙伴总数
      totalNum: [0, 0, 0],
      totalNumTrue: [0, 0, 0],
      // 项目类型  0、1、2
      type: [`代运维项目`, `代租赁项目`, `自有项目`],
      typeDefault: [`代运维项目`, `代租赁项目`, `自有项目`],
      // 根据项目类型显示不同的 symbol 颜色
      typeColor: [`#fd2e2d`, `#20be62`, `#f6f6f6`],
      // 是否只显示重点项目，标志位同 mapData
      importantFlag: [0, 1],
      switchText: ['切换至全部机组', '切换至重点机组'],
      // 是否展示详细信息
      showInfo: false,
      itemNum: '',
      companyName: '',
      showInfoFlag: [false, false],
      backBtnShow: false,
      // 地图中心
      center: [105, 39],
      // 地图缩放等级
      zoom: 1.2,
      // zoom: 0.95,
      myEChart: null,
      // 图例的 svg path
      svgPath: _svgPath,
      // 地图图例
      legend: {
        rentState: [
          {
            icon: 'mso-renting',
            name: '租赁中'
          },
          {
            icon: 'mso-waitingForRent',
            name: '待租赁'
          },
          {
            icon: 'mso-construction',
            name: '施工'
          },
          {
            icon: 'mso-maintenance',
            name: '检修'
          },
          {
            icon: 'mso-repair',
            name: '维修'
          }
        ],
        operatingState: [
          {
            name: '运行',
            color: '#20be62'
          },
          {
            name: '报警',
            color: '#fd2e2d'
          },
          {
            name: '停机',
            color: '#f6f6f6'
          },
          {
            name: '离线',
            color: '#808080'
          }
        ]
      }
    }
  },
  props: {
    // 地图数据 {机组编号:[经度、纬度、地址、机组编号、项目类型、所属项目、是否重点机组标志、机组名称、项目合同编码]}
    mapData: {
      type: Object,
      default: (_) => {},
      required: true
    },
    cb: {
      type: Function
    },
    contractRes: {
      type: Array,
      default: (_) => {
        return []
      }
    }
  },
  watch: {
    mapData(newVal, oldVal) {
      this.renderMap()
    },
    importantFlag(nVal, oVal) {
      this.renderMap()
    }
  },
  methods: {
    backHandler() {
      // 恢复地图初始状态
      this.myEChart.setOption({
        geo: {
          zoom: this.zoom,
          center: this.center
        }
      })
      this.backBtnShow = false
    },
    _switch() {
      this.backBtnShow = false
      if (this.importantFlag.length === 1) this.importantFlag = [0, 1]
      else this.importantFlag = [1]
    },
    renderMap() {
      this.myEChart ||
        (this.myEChart = this.$echarts.init(this.$refs.myEChart, null, {
          renderer: 'svg'
        }))
      const dataValue = this.dealWithData()
      // const symbolSize = this.importantFlag.length === 1 ? 12 : 7
      const symbolSize = 20
      const svgPath = this.svgPath
      const _this = this
      const option = {
        tooltip: {
          trigger: 'item'
        },
        // 地图背景
        geo: {
          map: 'china',
          roam: true,
          zoom: this.zoom,
          center: this.center,
          // 长宽比
          aspectScale: 0.9,
          scaleLimit: {
            min: this.zoom
            // max: 10
          },
          label: {
            show: false,
            fontSize: '10',
            color: 'rgba(0,0,0,0.7)'
          },
          itemStyle: {
            areaColor: this.bakColorMap,
            borderColor: '#389dff',
            borderWidth: 1,
            shadowBlur: 5,
            shadowOffsetY: 8,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0,0,0,0)'
          },
          emphasis: {
            itemStyle: {
              areaColor: '#184cff',
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              shadowBlur: 5,
              borderWidth: 0,
              shadowColor: 'rgba(0, 0, 0, 1)'
            },
            label: {
              show: false
            }
          }
        },
        series: [
          // {
          //   type: 'map',
          //   map: 'china',
          //   roam: true,
          //   data: dataValue,
          //   zoom: 1,
          //   center: [105, 36],
          //   geoIndex: -1,
          //   aspectScale: 0.75,
          //   showLegendSymbol: false,
          //   label: {
          //     show: false
          //   },
          //   itemStyle: {
          //     areaColor: this.bakColorMap,
          //     borderColor: '#389dff',
          //     borderWidth: 0.5
          //   },
          //   emphasis: {
          //     itemStyle: {
          //       areaColor: '#17008d',
          //       shadowOffsetX: 0,
          //       shadowOffsetY: 0,
          //       shadowBlur: 5,
          //       borderWidth: 0,
          //       shadowColor: 'rgba(0, 0, 0, 0.5)'
          //     },
          //     label: {
          //       show: false
          //     }
          //   }
          // },
          {
            name: '',
            // type: 'effectScatter',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: dataValue,
            symbol: (value, params) => {
              const rentState = value[13]
              const path = 'path://' + svgPath[_this.legend.rentState[rentState].icon.slice(4)]
              return path
            },
            // symbolSize: symbolSize,
            symbolSize: 13,
            // 注意：series.tooltip 仅在 tooltip.trigger 为 'item' 时有效。
            tooltip: {
              position: 'top',
              show: true,
              formatter: (params, ticket, cb) => {
                const item = params.data
                const value = item.value
                // 12 运行状态 13 租赁状态
                return `
                <div style="color:#b4e4ff">
                  <div>${item.name}</div>
                  <div>${_this.legend.operatingState[value[12]].name}
                  ,${_this.legend.rentState[value[13]].name}</div>
                  <div>经度：${value[0]}</div>
                  <div>纬度：${value[1]}</div>
                  <div>${value[2]}</div>
                  <div>所属项目：${value[5]}</div>
                </div>
                `
              },
              backgroundColor: 'rgba(2,20,4,1)'
            },
            encode: {
              value: 4
            },
            showEffectOn: 'emphasis',
            rippleEffect: {
              brushType: 'stroke',
              color: '#0efacc',
              period: 9,
              scale: 5
            },
            label: {
              formatter: '{b}',
              position: 'right',
              show: false
            },
            itemStyle: {
              color: (_) => {
                let color = `#f6f6f6`
                try {
                  const operatingState = _.data.value[12]
                  const color2 = _this.legend.operatingState[operatingState].color
                  color = color2
                  // const typeNum = _._data.value[4]
                  // let color2 = this.typeColor[typeNum]
                  // color = color2
                } catch (error) {
                  console.error(error)
                }
                return color
              },
              borderWidth: 10,
              shadowBlur: 2,
              shadowColor: '#333'
            },
            zlevel: 1
          }
        ]
      }
      this.myEChart.setOption(option)
      this.myEChart.on('georoam', (e) => {
        try {
          const zoom = this.myEChart.getOption().geo[0].zoom
          // const center = this.myEChart.getOption().geo[0].center
          if (
            Number(zoom) === 1
            //  && JSON.stringify(center) === JSON.stringify(this.center) 中心点偏离点过大
          ) {
            this.backBtnShow = false
          } else {
            this.backBtnShow = true
          }
        } catch (error) {}
      })
    },
    dealWithData() {
      const geoCoordMap = this.mapData
      const resData = []
      // 渲染图例数据
      const cardData = [0, 0, 0]
      // 渲染汇总数量
      const totalNum = [0, 0, 0]
      const totalNumJudge = []
      // 项目去重
      const itemJudge = new Set()
      // 服务企业
      const partnerNum = new Set()
      this.type = this.typeDefault
      for (const key in geoCoordMap) {
        const flag = geoCoordMap[key][6]
        if (this.importantFlag.includes(flag)) {
          const itemNum = geoCoordMap[key][4]
          // 机组
          const mchCode = geoCoordMap[key][3]
          const mchName = geoCoordMap[key][7]
          // 项目
          const itemName = geoCoordMap[key][5]
          const contractNum = geoCoordMap[key][8]
          // 合作伙伴===服务企业
          const partnerID = geoCoordMap[key][9]
          partnerNum.add(partnerID)
          resData.push({
            name: mchName,
            value: geoCoordMap[key]
          })
          if (!itemJudge.has(contractNum)) {
            cardData[itemNum]++
            itemJudge.add(contractNum)
          }
          if (!totalNumJudge.includes(mchCode)) {
            totalNum[0]++
            totalNumJudge.push(mchCode)
          }
          if (!totalNumJudge.includes(itemName)) {
            totalNum[1]++
            totalNumJudge.push(itemName)
          }
        }
      }
      totalNum[2] = partnerNum.size
      this.totalNumTrue = [...totalNum]
      totalNum[0] > 99999 && (totalNum[0] = 99999)
      totalNum[1] > 9999 && (totalNum[1] = 9999)
      totalNum[2] > 999 && (totalNum[2] = 999)
      this.cardData = cardData
      this.totalNum = totalNum

      // judge: 如果仅有一个企业且不止一个项目展示企业，如果仅有一个项目展示项目
      const itemArr = []
      const companyArr = []
      let itemNum = ''
      let companyName = ''
      this.showInfo = false
      const flag = [true, true]
      let index = true
      const newVal = this.mapData
      for (const ky in newVal) {
        const item = newVal[ky]
        itemArr.push(item[8])
        companyArr.push(item[9])
        if (index) {
          itemNum = item[8]
          companyName = item[9]
          index = false
        }
        if (item[8] !== itemNum) flag[0] = false
        if (item[9] !== companyName) flag[1] = false
      }
      // 仅一个项目
      if (flag[0]) {
        this.itemNum = itemNum
        this.companyName = companyName
        this.showInfo = true
        this.showInfoFlag = [true, true]
        this.type = [`服务类型`, `机组数量`, `重点监控`]
        const cardData = [0, 0, 0]
        const mapData = this.mapData
        for (const ky in mapData) {
          const element = mapData[ky]
          cardData[0] = this.typeDefault[element[4]].replace('项目', '')
          cardData[1]++
          if (element[6] === 1) cardData[2]++
        }
        this.cardData = cardData
      }
      // 一个企业
      if (flag[1] && !flag[0]) {
        this.companyName = companyName
        this.showInfo = true
        this.showInfoFlag = [false, true]
        this.type = [`项目数量`, `机组数量`, `重点监控`]
        const cardData = [0, 0, 0]
        const item = new Set()
        const mapData = this.mapData
        for (const ky in mapData) {
          const e = mapData[ky]
          item.add(e[5])
          cardData[1]++
          if (e[6] === 1) cardData[2]++
        }
        cardData[0] = item.size
        this.cardData = cardData
      }

      return resData
    },
    _resize() {
      // this.myEChart.resize()
      // this.renderMap()
    }
  },
  mounted() {
    this.renderMap()
    window.addEventListener(`resize`, (_) => {
      // 重载地图
      this._resize()
    })
  }
}
</script>
<style lang='less' scoped>
@float-padding-left: 15px;

div[hidden] {
  display: none;
}
.map-component {
  width: 100%;
  height: 100%;
  position: relative;
  // 左上角汇总模块
  &-total-num {
    top: 5px;
    z-index: 2;
    display: flex;
    color: white;
    position: absolute;
    left: @float-padding-left;
    & > div {
      margin: 0 15px 0 0;
      div:nth-child(2) {
        display: flex;
        & > div {
          margin: 0 10px 0 0;
        }
      }
    }
    .title {
      font-size: 1.6rem;
      font-weight: bolder;
      margin: 0 0 5px 0;
    }
  }
  // 汇总：展示企业和项目信息
  &-detail-info {
    top: 24px;
    z-index: 2;
    display: flex;
    color: white;
    font-size: 1.8rem;
    font-weight: 600;
    position: absolute;
    flex-direction: column;
    left: @float-padding-left;
    > div {
      margin: 0 0 8px 0;
    }
  }
  // 地图展示
  &-map {
    width: 100%;
    height: 100%;
    margin: 0 0 0 0;
  }
  // 地图左下角图例
  &-card {
    border: 1px solid #338eeb90;
    bottom: 15px;
    color: #dedede;
    font-size: 1.1rem;
    border-radius: 6px;
    position: absolute;
    left: -23px;
    background-color: #102447;
    transform: scale(0.75);
    transition: 0.5s;
    // &:hover {
    //   transform: scale(1);
    // }
    /deep/ .el-card__body {
      padding: 5px;
      width: 260px;
    }
    .title {
      font-size: 1.3rem;
    }
    svg {
      font-size: 1.7rem;
      margin: 0 4px 0 0;
    }
    .el-col {
      margin: 5px 0;
    }
    .container {
      display: flex;
      .color-box {
        width: 20px;
        height: 20px;
        border-radius: 3px;
        margin: 0 3px;
      }
    }
  }
  // 右上角切换重点机组
  &-switch {
    width: 155px;
    height: 35px;
    color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    font-weight: 500;
    display: flex;
    border-radius: 5px;
    cursor: pointer;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background: -webkit-gradient(linear, left top, left bottom, from(#769b8d), to(#233c85));
    background: linear-gradient(#769b8d, #233c85);
    position: absolute;
    right: 5px;
    top: 10px;
    font-size: 1.2rem;
  }
  &-reset {
    position: absolute;
    right: 50px;
    bottom: 25px;
    background: transparent;
    border: 0;
    color: white;
    font-size: 1.6rem;
  }
}
</style>



