<template>
  <div id="echarts"></div>
</template>

<script>
export default {
  watch: {
    numArr() {
      this.renderChart()
    },
    numArrOperation() {
      this.renderChart()
    }
  },
  name: 'ECharts',
  data() {
    return {
      chart: null,
      option: {}
      // 机组状态颜色（行业标准）
    }
  },
  inject: ['colorOperation'],
  props: {
    // 机组总数
    total: {
      type: Number,
      default: 0
    },
    // 各状态数量
    numArr: {
      type: Array,
      default: (_) => []
    },
    numArrOperation: {
      type: Array,
      default: (_) => []
    }
  },
  methods: {
    renderChart() {
      this.chart ||
        (this.chart = this.$echarts.init(document.getElementById('echarts'), null, {
          renderer: 'svg'
        }))
      const _this = this
      this.option = {
        // @description: pie chart use position to set layout
        // grid: [
        //   {
        //     show: true,
        //     left: '10%',
        //     right: '30%',
        //     bottom: '30%',
        //     top: '5%',
        //     backgroundColor: '#eee'
        //   },
        //   {
        //     show: true,
        //     left: '60%',
        //     right: '5%',
        //     bottom: 0,
        //     top: '55%',
        //     backgroundColor: '#888'
        //   }
        // ],
        cursor: 'default',
        title: {
          text: '机组总数\n' + this.total,
          textAlign: 'center',
          textStyle: {
            color: '#fff'
            // color: '#b2b9c4'
            // fontWeight: 'lighter'
          },
          left: '50%',
          top: '27%'
        },
        graphic: {
          elements: [
            {
              type: 'group',
              top: '77%',
              left: '71%',
              // z: 5,
              children: [
                {
                  type: 'text',
                  z: 5,
                  style: {
                    fill: '#fff',
                    textAlign: 'center',
                    text: `租赁中\n${this.numArr[1].value}`,
                    // 文字样式，此处只能通过该属性设置
                    font: 'bold 14px sans-serif'
                  }
                }
              ]
            }
          ]
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center',
          show: false
        },
        series: [
          // 租赁状态数据
          {
            name: '',
            left: '2%',
            top: 0,
            bottom: '35%',
            type: 'pie',
            radius: ['58%', '70%'],
            avoidLabelOverlap: true,
            itemStyle: {
              borderColor: '#0f2142',
              borderWidth: 2
            },
            // emphasis: {
            //   // disable: true, v5.3.0 supported
            //   scale: true,
            //   label: {
            //     show: true,
            //     fontSize: '40',
            //     fontWeight: 'bold'
            //   }
            // },
            label: {
              alignTo: 'edge',
              formatter: '{name|{b}}\n{value|{c} }',
              minMargin: 5,
              edgeDistance: 10,
              lineHeight: 15,
              rich: {
                value: {
                  fontSize: 12,
                  color: '#fff',
                  lineHeight: 23
                },
                name: {
                  fontSize: 15,
                  color: '#fff',
                  lineHeight: 23
                }
              }
            },
            labelLine: {
              length: 15,
              length2: 5,
              maxSurfaceAngle: 90,
              lineStyle: {
                width: 2,
                cap: 'butt'
              }
              // smooth:true
            },
            // 标签和引导线的回调
            labelLayout: function (params) {
              const isLeft = params.labelRect.x < _this.chart.getWidth() / 2
              const points = params.labelLinePoints
              // Update the end point.
              points[2][0] = isLeft
                ? params.labelRect.x
                : params.labelRect.x + params.labelRect.width
              points[1][0] = isLeft ? points[1][0] - 5 : points[1][0] + 10
              // points[0][0] = isLeft ? points[0][0] - 10 : points[0][0] + 10
              return {
                labelLinePoints: points
              }
            },
            data: this.numArr,
            emptyCircleStyle: {},
            emphasis: {
              scale: false
            }
            // cursor: 'default'
          },
          // 运行状态数据
          {
            name: '',
            left: '50%',
            top: '62%',
            right: 0,
            bottom: 0,
            type: 'pie',
            radius: ['0', '70%'],
            avoidLabelOverlap: true,
            itemStyle: {
              color: (params) => _this.colorOperation[params.dataIndex]
            },
            label: {
              alignTo: 'edge',
              formatter: '{name|{b}}\n{value|{c} 个}',
              minMargin: 1,
              edgeDistance: 10,
              lineHeight: 12,
              rich: {
                value: {
                  fontSize: 11,
                  color: '#fff'
                },
                name: {
                  fontSize: 12,
                  color: '#fff'
                }
              }
            },
            emphasis: {
              label: {
                show: true
              }
            },
            labelLine: {
              show: true,
              showAbove: true
            },
            data: this.numArrOperation,
            z: 3,
            emphasis: {
              scale: false
            }
            // cursor: 'default'
          },
          // the background color of second pie chart
          {
            name: 'operating state backPie',
            left: '50%',
            top: '62%',
            right: 0,
            bottom: 0,
            type: 'pie',
            radius: ['0', '70%'],
            avoidLabelOverlap: true,
            itemStyle: {
              borderColor: '#fff',
              borderWidth: 10
            },
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            emphasis: {
              label: {
                show: false
              },
              scale: false
            },
            labelLine: {
              show: false
            },
            data: this.numArrOperation,
            emphasis: {
              scale: false
            }
            // cursor: 'default'
          }
        ]
      }
      this.chart.setOption(this.option)
    }
  }
}
</script>

<style lang="less" scoped>
div:first-child {
  height: 100%;
  width: 100%;
}
</style>



